.Game {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  width: 100%;
  height: 100vh;
  background: #f7fafc;
  grid-gap: 10px;
  gap: 10px;
}

.action-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.main-title {
  margin-bottom: 10px;
  text-decoration: underline;
}

.show-game-detail-container-button {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  right: 0;
  top: 0;
  margin: 10px 20px;
}

.show-game-detail-container-button button {
  background-color: transparent;
  border: none;
  font-size: 25px;
  cursor: pointer;
}

.Grid {
  display: flex;
  flex-direction: column;
  padding: 30px;
}

tr {
  border: 1px solid #141c3a;
}

table {
  border: 3px solid #141c3a;
  border-collapse: collapse;
  margin: 0 auto;
}

td {
  border: 1px solid #141c3a;
  width: 30px;
  height: 30px;
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-weight : bold;

}

.right-border {
  border-right: 3px solid #141c3a;
}

.bottom-border {
  border-bottom: 3px solid #141c3a;
}

.cell-modifiable{
  cursor: pointer;
  color: blue;
}

.cell-invalid {
  background-color: #ff000054;
}

.cell-hinted{
  color: orange;
  -webkit-animation-name: hintAnimation;
          animation-name: hintAnimation;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
}

@-webkit-keyframes hintAnimation {
  from {background-color: orange;}
  to {background-color: transparent;background-color: initial;}
}

@keyframes hintAnimation {
  from {background-color: orange;}
  to {background-color: transparent;background-color: initial;}
}
.choice-container {
  display: flex;
  justify-content: space-around;
  flex: 1 1;
}

.choice {
  padding: 5px 15px;
  font-weight: 900;
  border: 2px solid;
  margin: 5px;
  border-radius: 10px;
  cursor: pointer;
}

.selected {
  background-color: #141c3a;
}
.selected-eraser {
  background: lightblue;
}

.selected p {
  color: white;
}

.noSelect {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.noSelect:focus {
    outline: none !important;
}

:root {
  --primary: #4628ff;
  --warning: #ffd028;
  --danger: #eb3f27;
  --success: #75fa83;
  --white: #fdfdfd;
  --dark: #181717;
}

.btn {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  border-radius: 10px;
  cursor: pointer;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;

  padding: 10px 20px;
  font-size: 18px;
}

.btn:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
}

/* Button colors and styles */
.btn--primary--solid {
  background-color: #4628ff;
  background-color: var(--primary);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--warning--solid {
  background-color: #ffd028;
  background-color: var(--warning);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--danger--solid {
  background-color: #eb3f27;
  background-color: var(--danger);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--success--solid {
  background-color: #75fa83;
  background-color: var(--success);
  color: #fdfdfd;
  color: var(--white);
  border: none;
}

.btn--primary--outline {
  background-color: transparent;
  border-color: #4628ff;
  border-color: var(--primary);
}
.btn--warning--outline {
  background-color: transparent;
  border-color: #ffd028;
  border-color: var(--warning);
}

.btn--danger--outline {
  background-color: transparent;
  border-color: #eb3f27;
  border-color: var(--danger);
}

.btn--success--outline {
  background-color: transparent;
  border-color: #75fa83;
  border-color: var(--success);
}

.InformationModal {
  width: 100vw;
  height: 100vh;
  background-color: rgba(200, 200, 200);
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-container {
  width: 500px;
  height: 500px;
  border-radius: 12px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  display: flex;
  flex-direction: column;
  padding: 25px;
}

.modal-container .modal-title {
  display: inline-block;
  text-align: center;
  margin-top: 10px;
}

.modal-close-btn-container {
  display: flex;
  justify-content: flex-end;
}

.modal-close-btn-container button {
  background-color: transparent;
  border: none;
  font-size: 25px;
  cursor: pointer;
}

.modal-container .modal-body {
  flex: 50% 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  font-size: 23px;
  text-align: justify;
  margin-bottom: 12px;
}

.modal-container .modal-body .links {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.7rem;
  text-align: center;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 10px;
}

.modal-container .modal-footer {
  flex: 20% 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

